@media (max-width: $breack-lg - 1) {
    .sidentifier {
        .content {
            padding-top: 20px;
            top: 0;
            transform: translate(0,0); } }

    .r-tabs-panel {
        border-bottom: 1px solid $gris_moyen; }

    section.content .creation-contrat .onglets .lien-noir {
        margin-top: 20px;
        position: relative;
        top: 0;
        transform: translate(0px); }

    section.content .creation-contrat .onglets .r-tabs-panel input.smaller[type="text"], section.content .creation-contrat .onglets .r-tabs-panel input.smaller[type="mail"], section.content .creation-contrat .onglets .r-tabs-panel input.smaller[type="phone"] {
        max-width: 58px; }
    section.content .creation-contrat .onglets .r-tabs-panel input.xsmall[type="text"], section.content .creation-contrat .onglets .r-tabs-panel input.xsmall[type="mail"], section.content .creation-contrat .onglets .r-tabs-panel input.xsmall[type="phone"] {
        max-width: 40px; }

    .ui-dialog {
        position: absolute;
        top: 10px; }
    .content table {
        margin-top: 30px; }


    section.content table tr td.boutons {
        height: auto;
        min-width: 253px;
        padding-bottom: 10px;
        a {
            margin-bottom: 5px; } }

    .onglets {
        .divers-container {
            .img-container {
                float: right; } } }

    section.content.mescontrats {
        background: #fff;
        width: 100%;
        padding-bottom: 0;
        ul.table-mobile {
            width: 100%;
            display: inline-block;
            li.contrat {
                transition: all 0.3s ease;
                cursor: pointer;
                width: 100%;
                padding: 30px 10px;
                margin: 0;
                display: inline-block;
                border-bottom: 1px solid #e6ecf3;
                background: #fff;
                position: relative;
                .icon-triangle {
                    color: #8c8c8c;
                    font-size: 8px;
                    position: absolute;
                    right: 19px;
                    top: 42px;
                    transform: rotate(90deg); }
                .icon-warning {
                    color: red; }
                .contrat-nom, .contrat-saisie {
                    width: 50%;
                    display: inline-block;
                    float: left;
                    padding-left: 30px;
                    span {
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 15px; } }
                .deroule {
                    display: none;
                    .contrat-nom, .contrat-saisie {
                        padding-top: 16px; }
                    .infos-complementaires {
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 18px; }
                    a {
                        color: #313238; } }
                .contrat-nom {
                    p {
                        color: #313238;
                        font-size: 18px;
                        font-weight: 700;
                        line-height: 15px; } }
                .contrat-saisie {
                    p {
                        color: #313238;
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 16px;
                        padding-right: 17px; } }
                .btns-actions {
                    width: 100%;
                    display: inline-block;
                    padding-top: 20px;
                    a {
                        border-radius: 0;
                        float: left;
                        display: inline-block;
                        width: calc(50% - 2px);
                        height: 39px;
                        font-size: 16px;
                        font-weight: 700;
                        line-height: 34px;
                        margin-left: 2px;
                        i.icon-hand {
                            font-size: 15px;
                            transform: translateY(1px); }
                        i.icon-check-mark {
                            font-size: 13px;
                            transform: translateY(0px); }
                        i.icon-oeil {
                            transform: translateY(6px); }
                        &:nth-child(2n+1) {
                            margin-right: 2px;
                            margin-left: 0px; } } }
                &:nth-child(2n+1) {
                    background: #f4f7fb; }
                &.active {
                    background: #dde2e8;
                    padding: 18px 10px 8px;
                    .icon-triangle {
                        transform: rotate(-90deg); } }
                &.rose {
                    background-color: #ffe9e9; }
                &.vert {
                    background-color: #ebffe5; } } } }

    .header {
        .logo {
            width: 305px;
            display: inline-block;
            float: left; } } }
