.lien-blanc {
    height: 42px;
    line-height: 49px;
    background-color: #fff;
    color: $bleu_fonce;
    letter-spacing: 0.15em;
    font-size: 13px;
    display: inline-block;
    border-radius: 20px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0 30px;
    position: relative;
    i {
        position: relative;
        transform: translateY(3px);
        margin-right: 5px;
        font-size: 24px;
        display: inline-block; }
    &:hover {
        color: $bleu_fonce;
        text-decoration: none; } }

.lien-noir {
    font-size: 13px;
    font-weight: 400;
    line-height: 25px;
    color: #313238;
    display: inline-block;
    height: 29px;
    border-radius: 20px;
    padding: 0 15px;
    border: 2px solid #4b4b4b;
    i {
        font-size: 13px;
        margin-right: 6px; }
    &:hover {
        color: #313238;
        text-decoration: none; } }
.lien {
    color: $blanc;
    letter-spacing: 0.15em;
    font-size: 13px;
    font-weight: 700;
    display: inline-block;
    position: relative;
    line-height: 20px;
    text-transform: uppercase;
    i {
        position: relative;
        margin-right: 5px;
        transform: translateY(3px);
        font-size: 24px;
        display: inline-block; }
    &:hover {
        color: $blanc;
        text-decoration: none; }
    &.active {
        position: relative;
        &:after {
            content: "";
            position: absolute;
            height: 2px;
            width: auto;
            background: #fff;
            left: 90px;
            right: 50px;
            bottom: -2px; } } }


.lien-vert {
